.button-up {
  position: fixed;
  top: 86%;
  right: 5%;
  z-index: 2;

  display: none;
  width: 50px;
  height: 50px;

  background-color: rgba(#bdbdbd, 0.6);
  border: 0;
  border-radius: 10px;
  outline: 0;
  transition-duration: 0.75s;

  svg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    margin: auto;

    fill: $color-black;
  }
}
